@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap');


* {
  margin: 0px;
  padding: 0px;
}

.home-img {
  width: 220px;
}

.topnav {
  display: flex;
  justify-content: space-between;
  padding-right: 20px;
  box-sizing: border-box;
  box-shadow: 3px 2px 6px rgba(39, 39, 39, 0.403);
}

.topnav-links {
  margin-top: 30px;
}

.topnav-links a {
  color: rgb(0, 110, 158);
  font-weight: 500;
  font-family: 'Source Sans Pro', sans-serif;
}

.topnav a {
  text-decoration: none;
  font-size: 19px;
  margin: 20px;
}

.topnav .icon {
  display: none;
  font-size: 30px;
  font-family: 600;
}


/* HOME CODE */

.home-content {
  width: 100%;
  min-height: 620px;
  padding-bottom: 50px;
  box-sizing: border-box;
  padding-top: 50px;
  padding-left: 50px;
  background-image: linear-gradient(45deg, rgb(18 10 22), rgb(0, 162, 255));
  display: flex;
}

.hcl {
  width: 75%;
}

.hcr-img {
  width: 90%;
  border: 2px solid #03334c;
}

.content-title {
  font-family: 'Rubik', sans-serif;
  font-size: 60px;
  font-weight: 800;
  color: white;
}

.content-title-info {
  /* font-family: 'Rubik', sans-serif; */
  width: 90%;
  font-size: 22px;
  color: white;
}

.btn-build {
  padding: 16px 38px;
  font-size: 22px;
  border-radius: 9px;
  background-image: linear-gradient(45deg, #ae68db, #009cff);
  color: white;
  border: 2px solid #03334c;
  margin-top: 40px;
}

.btn-build:hover {
  background-image: linear-gradient(45deg, #9f3add, #0086d9);
}

/* Carousel Code */
.ex-resume {
  width: 100%;
  height: auto;
  background-image: linear-gradient(45deg, rgb(18 10 22), rgb(0, 162, 255));
  padding: 60px 2%;
  text-align: center;
  /* background-color: #009cff; */
  /* background-image: url('./component/images/back1.jpg');
  background-size: cover;
  padding: 5%; */
}


.home-back {
  width: 50%;
  height: 500px;
  overflow: hidden;
  box-sizing: border-box;
  margin: 10px;
}

.home-back-img {
  width: 100%;
  min-height: 500px;
  margin: auto;
}

.info-1 {
  background-image: linear-gradient(45deg, #ae68db, #009cff);
}

.home-back-head {
  font-family: 'Rubik', sans-serif;
  font-size: 58px;
  color: white;
}

.content2 {
  display: flex;
  justify-content: center;
  padding-top: 50px;
}

.c2l {
  width: 30%;
  text-align: center;
  margin: 5px;
}

.c2l-img {
  width: 250px;
  margin-bottom: 20px;
}

.c2l-p {
  font-size: 17px;
  width: 80%;
  margin: auto;
  font-weight: 600;
}

.c2l-h {
  font-weight: 600;
  font-family: 'Rubik', sans-serif;
  color: #03334c;
  margin: 8px 0px 20px 0px;
}

/* FORM CSS */
.build-form {
  background-image: linear-gradient(45deg, rgb(18 10 22), rgb(0, 162, 255));
}

.build-form-content {
  /* display: flex; */
  box-sizing: border-box;
  width: 80%;
  margin: auto;
  padding-bottom: 30px;
}

.form-comp-flex {
  display: flex;
}

.form-comp-space {
  margin-right: 15px;
  width: 35%;
}

.bfc-l {
  width: 100%;
  padding-top: 60px;
  margin: 0px auto;
  /* display: none; */
}

.form1-label {
  font-weight: bold;
  font-size: 18px;
  display: block;
  padding: 20px 0px 10px 0px;
  color: white;
}

.form1-input {
  padding: 8px 10px;
  font-size: 18px;
  outline: none;
  border: 2px solid white;
  width: 100%;
}

/* Template 1 code */
.bfc-r {
  width: 100%;
  box-sizing: border-box;
  padding-top: 100px;
  padding-left: 40px;
  /* display: none; */
}

.template {
  background-color: #0086d9;
  width: 100%;
  height: 300px;
}

@media screen and (max-width: 850px) {
  .topnav.responsive {
    box-shadow: none;
  }

  .topnav-links {
    margin-top: 10px;
  }

  .topnav-links a {
    display: none;
  }

  .topnav a.icon {
    display: block;
  }


  .topnav.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
    margin: 0px 40px;
  }

  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }

  .topnav.responsive {
    float: none;
    position: relative;
    display: block;
    width: 100%;
    text-align: left;
  }

  .home-back,
  .home-back-img {
    height: 500px;
  }

  .form-comp-space {
    width: 50%;
  }

  .home-content,
  .content2 {
    display: block;
  }

  .content-title {
    font-size: 35px;
  }

  .content-title-info {
    font-size: 18px;
  }

  .btn-build {
    font-size: 18px;
    padding: 12px 19px;
    margin-bottom: 20px;
  }

  .content2,
  .c2l {
    width: 100%;
    box-sizing: border-box;
    padding-top: 10px;
  }

  .c2l-img {
    width: 130px;
  }

  .home-back {
    width: 100%;
  }

  .home-back-img {
    width: 80%;
  }
}